.icon-top{
    position: absolute;
    top: 10px;
    left: 10px;
}

.icon-bottom {
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.season-display.winter i {
    color: #043478;
}
.season-display.spring i {
    color: #F3F566;
}
.season-display.summer i {
    color: #D31701;
}
.season-display.fall i {
    color: #70C0FF;
}

.season-display {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.winter {
    /* ff 3.6+ */
    background:-moz-radial-gradient(ellipse at 50% -20%, rgba(3, 169, 244, 1) 0%, rgba(189, 240, 255, 1) 50%, rgba(3, 169, 244, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(ellipse at 50% -20%, rgba(3, 169, 244, 1) 0%, rgba(189, 240, 255, 1) 50%, rgba(3, 169, 244, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(ellipse at 50% -20%, rgba(3, 169, 244, 1) 0%, rgba(189, 240, 255, 1) 50%, rgba(3, 169, 244, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(ellipse at 50% -20%, rgba(3, 169, 244, 1) 0%, rgba(189, 240, 255, 1) 50%, rgba(3, 169, 244, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(ellipse at 50% -20%, rgba(3, 169, 244, 1) 0%, rgba(189, 240, 255, 1) 50%, rgba(3, 169, 244, 1) 100%);
}

.spring {
    /* ff 3.6+ */
    background:-moz-radial-gradient(ellipse at 50% -20%, rgba(0, 150, 136, 1) 0%, rgba(178, 255, 180, 1) 50%, rgba(0, 150, 136, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(ellipse at 50% -20%, rgba(0, 150, 136, 1) 0%, rgba(178, 255, 180, 1) 50%, rgba(0, 150, 136, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(ellipse at 50% -20%, rgba(0, 150, 136, 1) 0%, rgba(178, 255, 180, 1) 50%, rgba(0, 150, 136, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(ellipse at 50% -20%, rgba(0, 150, 136, 1) 0%, rgba(178, 255, 180, 1) 50%, rgba(0, 150, 136, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(ellipse at 50% -20%, rgba(0, 150, 136, 1) 0%, rgba(178, 255, 180, 1) 50%, rgba(0, 150, 136, 1) 100%);

}

.summer {
    /* ff 3.6+ */
    background:-moz-radial-gradient(ellipse at 50% -20%, rgba(220, 35, 35, 1) 0%, rgba(255, 250, 178, 1) 50%, rgba(220, 35, 35, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(ellipse at 50% -20%, rgba(220, 35, 35, 1) 0%, rgba(255, 250, 178, 1) 50%, rgba(220, 35, 35, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(ellipse at 50% -20%, rgba(220, 35, 35, 1) 0%, rgba(255, 250, 178, 1) 50%, rgba(220, 35, 35, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(ellipse at 50% -20%, rgba(220, 35, 35, 1) 0%, rgba(255, 250, 178, 1) 50%, rgba(220, 35, 35, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(ellipse at 50% -20%, rgba(220, 35, 35, 1) 0%, rgba(255, 250, 178, 1) 50%, rgba(220, 35, 35, 1) 100%);

}

.fall {
    /* ff 3.6+ */
    background:-moz-radial-gradient(ellipse at 50% -20%, rgba(59, 16, 153, 1) 0%, rgba(0, 181, 233, 1) 50%, rgba(59, 16, 153, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(ellipse at 50% -20%, rgba(59, 16, 153, 1) 0%, rgba(0, 181, 233, 1) 50%, rgba(59, 16, 153, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(ellipse at 50% -20%, rgba(59, 16, 153, 1) 0%, rgba(0, 181, 233, 1) 50%, rgba(59, 16, 153, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(ellipse at 50% -20%, rgba(59, 16, 153, 1) 0%, rgba(0, 181, 233, 1) 50%, rgba(59, 16, 153, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(ellipse at 50% -20%, rgba(59, 16, 153, 1) 0%, rgba(0, 181, 233, 1) 50%, rgba(59, 16, 153, 1) 100%);
}